.sidebar {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  background-color: #0f0f0f;
  position: fixed;
  left: 0;
  top: 0;
  width: 25rem;
}

.sidebar .logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #000;
  width: 100%;
}

.sidebar .logo .home_link {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sidebar .logo .heading__description {
  color: #fff;
  font-size: 2rem;
  padding-bottom: 2rem;
}

.sidebar .logo .logo_img {
  width: 12rem;
  height: auto;
  fill: #fff;
  pointer-events: none;
}

.sidebar .logo .heading__logo {
  color: #fff;
  font-size: 5rem;
  display: inline-block;
  pointer-events: none;
}

.sidebar .sidebar_lists-box {
  width: 100%;
  margin: auto;
}

.sidebar .sidebar_lists {
  display: flex;
  flex-direction: column;
  color: white;
  font-size: 2.4rem;
}

.sidebar .sidebar_lists .list {
  display: flex;
  width: 100%;
  border-top: #454545 solid 1px;
  align-self: center;
}

.sidebar .sidebar_lists .list.list:last-child {
  border-bottom: #454545 solid 1px;
}

.sidebar .sidebar_lists .list a {
  text-transform: uppercase;
  font-size: 3.2rem;
  display: inline-block;
  margin: auto;
  padding: 2rem 0;
}

.lists_link {
  cursor: pointer;
}

.lists_link:hover {
  color: #3bc9db;
  transition: all 0.3s;
}

.sidebar__icons-box {
  display: flex;
  gap: 2rem;
  margin-top: 4rem;
  justify-content: center;
}

.sidebar__icon_link {
  color: #3bc9db;
  fill: red;
}

.logo-icon {
  width: 5rem;
  height: 4rem;
  color: #3bc9db;
}

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  -webkit-touch-callout: none;
  user-select: none;
}

.lists {
  list-style: none;
}

.container {
  padding-left: 25rem;
}

a {
  text-decoration: none;
  color: #fff;
}

.section-full {
  display: flex;
  flex-direction: column;
  height: 100vh;
  gap: 5rem;
  background-color: #131313;
}

.section-full .blast-root {
  font-size: 8rem;
  color: white;
  cursor: default;
}

.section-full .blast {
  display: inline-block;
  margin-right: -2rem;
  color: #fff;
  cursor: default;
}

.section-full .right_white_space {
  margin-right: 2rem;
}

.section-full .animation {
  display: inline-block;
  animation: letteranim 0.5s ease-in;
  animation-iteration-count: 1;
}

.section-box {
  width: 100%;
  margin: auto;
  padding-left: 6rem;
  padding-right: 4rem;
}

.description-box {
  display: flex;
  flex-direction: column;
  gap: 3rem;
  margin-top: 6rem;
}

p {
  font-size: 3rem;
  color: #fff;
}

.heading-2 {
  color: #fff;
  font-size: 2.8rem;
  font-weight: 400;
  margin-top: 3rem;
}

.btn {
  display: inline-block;
  text-align: center;
  color: #3bc9db;
  font-size: 3.6rem;
  padding: 2rem 4rem;
  margin-top: 3.5rem;
  border: solid 1px #3bc9db;
  background-color: #212529;
  background-image: linear-gradient(130deg, transparent 0%, transparent 50%, #3bc9db 50%);
  background-size: 230%;
  transition: all 0.5s;
}

.btn:hover, .btn:active {
  background-position: 100%;
  color: #fff;
  border: solid 1px #fff;
}

.section-contact {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0;
}

.contact-form {
  margin-top: 4rem;
}

.contact-form ul {
  list-style: none;
  border: none;
  display: grid;
  gap: 1.2rem;
  grid-template-columns: 1fr 1fr;
}

.contact-form ul li {
  border-bottom: #131313 solid 0.3rem;
}

.contact-form ul li .input__field {
  border: none;
  outline: none;
  padding-left: 2rem;
  height: 6rem;
  width: 100%;
  background-color: #434343;
  font-size: 2.4rem;
  color: #fff;
}

.contact-form ul li .input__field:active {
  border: none;
  outline: none;
}

.contact-form li:nth-child(3) {
  grid-column: 1 / -1;
}

.contact-form li:nth-child(4) {
  grid-column: 1 / -1;
}

.contact-form li:nth-child(4) textarea {
  padding-top: 1.2rem;
  height: 20rem;
}

.border__bottom-animation {
  background-color: #3bc9db !important;
  animation: border_anim 0.4s linear;
  animation-fill-mode: forwards !important;
  backface-visibility: hidden;
}

.border__bottom-animation-red {
  background-color: red !important;
  animation: border_anim 0.4s linear;
  animation-fill-mode: forwards !important;
  backface-visibility: hidden;
}

@keyframes border_anim {
  0% {
    width: 0%;
  }
  100% {
    width: 100%;
  }
}

.animation_helper {
  background-color: #131313;
  height: 0.3rem;
  display: block;
}

.second-page-contact {
  width: 99%;
  height: 99%;
  padding-right: 1rem;
}

.section-about {
  background-color: #131313;
  display: grid;
  grid-template-columns: 1fr 1.2fr;
}

.text_link {
  color: #3bc9db;
  cursor: pointer;
}

.second-box-about {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flip-card {
  width: 25rem;
  height: 40rem;
  perspective: 100rem;
  border-radius: 1rem;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
  border-radius: 1rem;
}

.card-img {
  margin-top: 4rem;
  max-width: 20rem;
  height: auto;
  border-radius: 50%;
}

.card_link {
  margin-top: 4rem;
  display: inline-block;
  font-size: 3.6rem;
  color: #bbb;
  border-bottom: 1px solid #bbb;
}

.heading_card {
  font-size: 3.6rem;
  color: #bbb;
  padding-top: 4rem;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front,
.flip-card-back {
  position: absolute;
  border-radius: 1rem;
  width: 100%;
  height: 100%;
  /* Safari */
  backface-visibility: hidden;
}

.flip-card-back {
  background-color: #464646;
  transform: rotateY(180deg);
}

.flip-card-front {
  background-color: #464646;
}

@keyframes letteranim {
  0% {
    transform: scale(1, 1);
    color: #3bc9db;
  }
  60% {
    transform: scale(1.5, 0.7);
  }
  88% {
    transform: scale(0.8, 1.2);
  }
  100% {
    transform: scale(1, 1);
  }
}

@keyframes texttypping {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  to {
    width: 100%;
    opacity: 1;
  }
}

@keyframes texttypping-about {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  to {
    width: 100%;
    opacity: 1;
  }
}

@keyframes texttypping-my-skills {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  to {
    width: 100%;
    opacity: 1;
  }
}

@keyframes texttypping-contact {
  0% {
    width: 0;
  }
  1% {
    opacity: 1;
  }
  to {
    width: 100%;
    opacity: 1;
  }
}

.animation-observer .blast-child:first-child {
  opacity: 0;
  animation: texttypping 2s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
}

.animation-observer .blast-child:nth-child(2) {
  opacity: 0;
  animation: texttypping 2s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
  animation-delay: 0.4s;
}

.animation-observer .blast-child:nth-child(3) {
  opacity: 0;
  animation: texttypping 3s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
  animation-delay: 1.8s;
}

.animation-blast-child-about {
  opacity: 0;
  animation: texttypping-about 2s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
}

.animation-blast-child-contact {
  opacity: 0;
  animation: texttypping-contact 2s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
}

.animation-observer-2-rows .blast-child:first-child {
  opacity: 0;
  animation: texttypping-my-skills 2s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
}

.animation-observer-2-rows .blast-child:nth-child(2) {
  opacity: 0;
  animation: texttypping-my-skills 2s steps(40, end);
  animation-iteration-count: 1;
  overflow: hidden;
  white-space: nowrap;
  animation-fill-mode: forwards;
  animation-delay: 1s;
}

.section-my-skills {
  display: grid;
  grid-template-columns: 1fr 1.2fr;
}

.second-page-my-skills {
  display: flex;
  align-items: flex-end;
}

.anim-box {
  width: 60rem;
  height: 60rem;
  margin: auto 0;
  display: grid;
  grid-template-columns: 6rem;
  grid-template-rows: 6rem;
  place-content: center;
}

.span-about {
  color: #3bc9db;
  font-size: 6rem;
  grid-area: 1/1;
  line-height: 6rem;
  text-align: center;
  animation: spin 16s var(--d, 0s) linear infinite;
  /* duration = 12s, numbor of item = 6 so a delay of 12/6 = 2s */
  transform: rotate(0deg) translate(20rem) rotate(0deg);
}

@keyframes spin {
  0% {
    opacity: 0;
  }
  1% {
    opacity: 0.01;
  }
  60% {
    opacity: 1;
  }
  80% {
    opacity: 0.8;
  }
  95% {
    opacity: 0;
  }
  100% {
    opacity: 0;
    transform: rotate(1turn) translate(45rem) rotate(-1turn);
  }
}
